import React, {  Component } from 'react';
import { LoadState } from '../../../utils/Enums';
import Badge from 'react-bootstrap/Badge';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import { botometerUrl } from '../../../utils/Urls';
import { CircularProgressbar } from 'react-circular-progressbar';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import 'react-circular-progressbar/dist/styles.css';

export default class Botometer extends Component {
  state = {
    loadState: LoadState.LOADING,
    botometerData: null
  };

  calculateConfidence(botometerData) {
    return ((1 - (botometerData.display_scores.english / 5)) * 100).toFixed(1);
  }

  async componentDidMount() {
    try {
      const response = await fetch(`${botometerUrl}?tweetUrl=${this.props.tweetUrl}`)
      const responseJson = await response.json();
      const botometerData = responseJson;
      botometerData.confidence = this.calculateConfidence(botometerData);
      this.setState({
        loadState: LoadState.LOADED,
        botometerData: botometerData
      });
    }
    catch (error) {
      console.error(error);
      this.setState({
        loadState: LoadState.FAILED,
        botometerData: null
      });
    }
  }

  renderTooltip(props) {
    return (
      <Tooltip id='button-tooltip' {...props}>
        Botometer checks the activity of a Twitter account and gives it a score based on how likely the account is to be a bot. Higher scores are more bot-like.
      </Tooltip>
    );
  }

  renderConfidenceBar() {
    return (
        <span>
            <div style={{width: 75}}>
                <CircularProgressbar value={this.state.botometerData.confidence} text={`${this.state.botometerData.confidence}%`} />
            </div>
            <div style={{width: 30}}/>
        </span>)
  }

  render() {
    let result = '';
    if (this.state.loadState === LoadState.LOADING) {
      result = (<div>Loading Botometer score...</div>);
    } else if (this.state.loadState === LoadState.FAILED) {
      result = (<div>Failed to retrieve the Botometer score. Please try again later.</div>);
    } else if (this.state.loadState === LoadState.LOADED) {
      result = (
        <div>
          The author of this tweet is <b>not</b> a bot account.&nbsp;
          (<b>{this.state.botometerData.confidence}%</b> confidence)
        </div>
      );
    }
    return (
      <Row>
        <Col>
        <h4>
          Botometer Score &nbsp;
          <OverlayTrigger
            placement='left'
            delay={{ show: 100, hide: 100 }}
            overlay={this.renderTooltip}
          >
            <Badge variant='secondary'>?</Badge>
          </OverlayTrigger>
        </h4>
        {result}
        </Col>
        {this.state.loadState === LoadState.LOADED && this.renderConfidenceBar()}
      </Row>
    );
  }
}
