import React, {  Component } from 'react';
import Disqus from "disqus-react";
import "../../../styles/App.css"
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import Button from '@material-ui/core/Button';
import Row from "react-bootstrap/Row";

export default class Comments extends Component {
    state = {
        displayingComments: false
    }

    renderExpandComments(disqusConfig) {
        const numComments = <Disqus.CommentCount
            shortname='app-thoracle-site'
            config={disqusConfig}
        />

        return (<Button
            onClick={() => { this.setState({ displayingComments: true}) }}
            startIcon={<ExpandMoreIcon/>}>
            <div className='show-comments-button-label'>Show Forum <i style={{color: '#A9A9A9'}}>{numComments}</i></div>
        </Button>)
    }

    renderCollapseComments() {
        return (<Button
            onClick={() => { this.setState({ displayingComments: false}) }}
            startIcon={<ExpandLessIcon/>}>
            <div className='show-comments-button-label'>Hide Forum</div>
        </Button>)
    }

    render() {
        const disqusShortname = "app-thoracle-site"
        let disqusConfig = {
            url: this.props.pageUrl,
            identifier: this.props.disqusId,
            title: this.props.disqusId
        }

        return (
            <div>
                <Row className={'justify-content-center'}>
                    {this.state.displayingComments ? this.renderCollapseComments() : this.renderExpandComments(disqusConfig)}
                </Row>
                <div>
                    {this.state.displayingComments && <Disqus.DiscussionEmbed
                        shortname={disqusShortname}
                        config={disqusConfig}
                    /> }
                </div>
            </div>
        )
    }
}
